import React, { Component, useContext} from 'react'
import ReactDOM from 'react-dom';
import Context from '../components/Context';
import Loading from '../components/Loading';

import SideMenu from '../components/SideMenu'
import WelcomeSection from '../components/WelcomeSection'
import FridgeMagnetsSection from '../components/FridgeMagnetsSection'

const App = (props) => {
  const ready = (auth0Login, u, s, f) =>(
    <div>
      <SideMenu/>
      <WelcomeSection user={u}/>
      <FridgeMagnetsSection favoriteStores={u} featuredStores={f}/>
    </div>
  )
  return(
    <Context.Consumer>
      {context => (
        !context.isReady
        ?<Loading/>
        :ready(context.loginWithRedirect ,context.user, context.stores, context.featuredStores)
      )}
    </Context.Consumer>
  )
}

export default App;
